import React from 'react';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import Panel from '../../../components/Panel/Panel';
import Input from '../../../components/Ui/Input/Input';
import { t } from '../../../i18n';
import { SIMULATOR_NAMESPACE } from '../constants';
import TvaRateByCountry from '../../../components/TvaRate/tvaRateByCountry';
import { getSignaletic } from '../../../redux/accounts/accounts.actions';
import { getSelectedAccountNumber } from '../../../redux/accounts/accounts.selectors';
import { countries } from '../constants';

class LoanSimulator extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      creditAmount: 250000000,
      enableAmountSlider: true,
      rate: 4.5,
      duration: 69,
      monthlyPayment: 5000000,
      tva: true,
      tvaRate: parseFloat(30).toFixed(2),
      chosenCountry: 'ivoryCoast',
      showCountryList: false,
    };
    this.tvaRate = parseFloat(30).toFixed(2);
  }
  _toggleCountryList = () => {
    this.setState({ showCountryList: !this.state.showCountryList });
  };
  _onChangeAmountSlider = e => {
    if (this.state.enableAmountSlider) {
      let newCreditAmount;
      if (e.target && e.target.nodeName === 'INPUT') {
        newCreditAmount = parseInt(e.target.value);
      } else {
        newCreditAmount = e.value;
      }
      this.setState({ creditAmount: newCreditAmount }, this._simulateDuration);
    }
  };
  _onChangeRateSlider = e => {
    let newRate;
    if (e.target && e.target.nodeName === 'INPUT') {
      newRate = e.target.value;
    } else {
      newRate = e.value;
    }
    this.setState(
      { rate: newRate },
      this.state.enableAmountSlider
        ? this._simulateAmount
        : this._simulateMonthlyPayment,
    );
  };
  _onChangeDurationSlider = e => {
    let newDuration;
    if (e.target && e.target.nodeName === 'INPUT') {
      newDuration = parseInt(e.target.value);
    } else {
      newDuration = e.value;
    }
    this.setState(
      { duration: newDuration },
      this.state.enableAmountSlider
        ? this._simulateAmount
        : this._simulateMonthlyPayment,
    );
  };
  _onChangeMonthlyPaymentSlider = e => {
    if (!this.state.enableAmountSlider) {
      let newMonthlyPayment;
      if (e.target && e.target.nodeName === 'INPUT') {
        newMonthlyPayment = parseInt(e.target.value);
      } else {
        newMonthlyPayment = e.value;
      }
      this.setState(
        { monthlyPayment: newMonthlyPayment },
        this._simulateDuration,
      );
    }
  };
  _toggleTVA = tva => {
    this.setState(
      { tva: tva },
      this.state.enableAmountSlider
        ? this._simulateAmount
        : this._simulateMonthlyPayment,
    );
  };
  _simulateDuration = () => {
    const simulationValues = {
      tva: this.state.tva,
      rate: this.state.rate,
      creditAmount: this.state.creditAmount,
      monthlyPayment: this.state.monthlyPayment,
    };
    const tvaValue = simulationValues.tva ? this.tvaRate / 100 : 0;
    const x =
      1 -
      (1 + tvaValue) *
        (simulationValues.rate / 1200) *
        (this.state.creditAmount / this.state.monthlyPayment);
    const y = 1 + (1 + tvaValue) * (simulationValues.rate / 1200);
    const duration = Math.log(x) / Math.log(1 / y);
    this.setState({ duration: duration });
  };
  _simulateAmount = () => {
    const simulationValues = {
      tva: this.state.tva,
      rate: this.state.rate,
      duration: this.state.duration,
      monthlyPayment: this.state.monthlyPayment,
    };
    const tvaValue = simulationValues.tva ? this.tvaRate / 100 : 0;
    const x = (1 + tvaValue) * (simulationValues.rate / 1200);
    const y = Math.pow(1 + x, simulationValues.duration);
    const z = (1 - 1 / y) / x;
    const creditAmount = simulationValues.monthlyPayment * z;
    this.setState({ creditAmount: creditAmount });
  };
  _simulateMonthlyPayment = () => {
    const simulationValues = {
      creditAmount: this.state.creditAmount,
      tva: this.state.tva,
      rate: this.state.rate,
      duration: this.state.duration,
    };
    const tvaValue = simulationValues.tva ? this.tvaRate / 100 : 0;
    const x = (1 + tvaValue) * (simulationValues.rate / 1200);
    const y = Math.pow(1 + x, simulationValues.duration);
    const z = x / (1 - 1 / y);
    const monthlyPayment = simulationValues.creditAmount * z;
    this.setState({ monthlyPayment: monthlyPayment });
  };
  componentDidMount() {
    this.state.enableAmountSlider
      ? this._simulateAmount()
      : this._simulateMonthlyPayment();
    if (this.props.signaletic.fullName === '') this.props.getSignaletic();
    const country = countries.filter(
      country => country.codeBank === this.props.bank,
    )[0];
    if (country) {
      this.setState({
        chosenCountry: country.country,
        tvaRate: country.tvaRate,
      });
    }
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevState.tvaRate !== this.state.tvaRate) {
      this.tvaRate =
        this.state.tvaRate === ''
          ? 0
          : parseFloat(this.state.tvaRate).toFixed(2);
      this.forceUpdate();
    }
    if (prevProps.bank !== this.props.bank) {
      const country = countries.filter(
        country => country.codeBank === this.props.bank,
      )[0];
      if (country) {
        this.setState({
          chosenCountry: country.country,
          tvaRate: country.tvaRate,
        });
      }
    }
  }
  render() {
    return (
      <div className="main-content container-fluid">
        <div className="col-sm-12">
          <Panel title={t(`${SIMULATOR_NAMESPACE}simulateYourCredit`)}>
            <TvaRateByCountry
              showCountryList={this.state.showCountryList}
              toggleCountryList={this._toggleCountryList}
              chosenCountry={this.state.chosenCountry}
              onChangeCountry={(country, tva) => {
                this.setState({ chosenCountry: country, tvaRate: tva });
              }}
              _toggleTVA={tva => {
                this._toggleTVA(tva);
              }}
            />
            <div className="simulator_wrapper design-two">
              <form>
                <Input
                  unit={t(`${SIMULATOR_NAMESPACE}currency`)}
                  inputField={t(`${SIMULATOR_NAMESPACE}creditAmount`)}
                  elementType="SimulatorSlider"
                  minValue={2000}
                  maxValue={500000000}
                  value={parseInt(this.state.creditAmount)}
                  onchange={this._onChangeAmountSlider}
                  lock
                  checked={this.state.enableAmountSlider}
                  onLockChange={e =>
                    this.setState({
                      enableAmountSlider: e.target.checked,
                    })
                  }
                />
                <Input
                  floatNumber
                  step={0.1}
                  unit="%"
                  inputField={t(`${SIMULATOR_NAMESPACE}rate`)}
                  elementType="SimulatorSlider"
                  minValue={1}
                  maxValue={18}
                  value={this.state.rate}
                  onchange={this._onChangeRateSlider}
                />
                <Input
                  unit={t(`${SIMULATOR_NAMESPACE}months`)}
                  inputField={t(`${SIMULATOR_NAMESPACE}duration`)}
                  elementType="SimulatorSlider"
                  minValue={1}
                  maxValue={300}
                  value={parseInt(this.state.duration)}
                  onchange={this._onChangeDurationSlider}
                />
                <Input
                  unit={t(`${SIMULATOR_NAMESPACE}currencyByMonth`)}
                  inputField={t(`${SIMULATOR_NAMESPACE}monthly`)}
                  elementType="SimulatorSlider"
                  minValue={1}
                  maxValue={50000000}
                  value={parseInt(this.state.monthlyPayment)}
                  onchange={this._onChangeMonthlyPaymentSlider}
                  lock
                  checked={!this.state.enableAmountSlider}
                  onLockChange={e =>
                    this.setState({
                      enableAmountSlider: !e.target.checked,
                    })
                  }
                />
              </form>
            </div>
          </Panel>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => ({
  getSignaletic: () => {
    dispatch(getSignaletic());
  },
});
const mapStateToProps = state => ({
  selectedAccountNumber: getSelectedAccountNumber(state),
  bank: state.Accounts.signaletic.branch.bank,
  signaletic: state.Accounts.signaletic,
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(LoanSimulator),
);
