import React from 'react';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';
import HeadNav from '../../../components/PageHeadNav/PageHeadNav';
import PageHeader from '../../../components/PageHeader/PageHeader';
import { t } from '../../../i18n';
import { SIMULATOR_NAMESPACE } from '../constants';
import LoanSimulator from './LoanSimulator';

const Feature = () => (
  <div className="page-content">
    <div className="page-content_side">
      <PageHeader text={t(`${SIMULATOR_NAMESPACE}title`)}>
        <li key="1">
          <Link to="/dashboard">
            {t(`${SIMULATOR_NAMESPACE}breadCrumbFirstLink`)}
          </Link>
        </li>
        <li key="2">
          <Link to="/simulator">
            {t(`${SIMULATOR_NAMESPACE}breadCrumbSecondLink`)}
          </Link>
        </li>
        <li className="active" key="3">
          {t(`${SIMULATOR_NAMESPACE}title`)}
        </li>
      </PageHeader>
      <div className="page-content_inner">
        <HeadNav>
          <li>
            <Link className="active" to="#">
              {t(`${SIMULATOR_NAMESPACE}tabTitle`)}
            </Link>
          </li>
        </HeadNav>
        <LoanSimulator />
      </div>
    </div>
  </div>
);
export default withTranslation()(Feature);
