import { connect } from 'react-redux';
import Simulator from './components/feature';
import { activateSimulatorTab } from '../../redux/menu/menu.actions';

const mapStateToProps = state => ({
  activeTab: state.Menu.simulatorActiveTab,
});
const mapDispatchToProps = dispatch => ({
  activateTab: activeTab => {
    dispatch(activateSimulatorTab(activeTab));
  },
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Simulator);
