import React from 'react';
import { withTranslation } from 'react-i18next';
import { t } from 'i18n';
import { SIMULATOR_NAMESPACE } from '../../features/Simulator/constants';
import { countries } from '../../features/Simulator/constants';

class TvaRateByCountry extends React.PureComponent {
  render() {
    const countryOptions = countries.map(country => (
      <li
        className="p-dropdown-item"
        onClick={() => {
          this.props.onChangeCountry(country.country, country.tvaRate);
          this.props._toggleTVA(country.tvaRate);
        }}
      >
        <img
          alt="ivoryCoast"
          src={require(`../../assets/img/countries/${country.country}.svg`)}
          width="24"
        />
      </li>
    ));
    return (
      <div className="tvaRate">
        <label htmlFor="tvaRate">{t(`${SIMULATOR_NAMESPACE}countries`)}</label>
        <div className="list-pay" onClick={this.props.toggleCountryList}>
          <span className="value">
            <img
              alt="ivory-coast"
              src={require(`../../assets/img/countries/${this.props.chosenCountry}.svg`)}
              width="24"
            />
          </span>
          <ul
            className={
              this.props.showCountryList ? 'hidden-list d-block' : 'hidden-list'
            }
          >
            {countryOptions}
          </ul>
        </div>
      </div>
    );
  }
}
export default withTranslation()(TvaRateByCountry);
